section#subscribe{
    background: #262d3d;
    text-align: center;
    color: var(--color-white);
}

.subscribe h2{
    font-size: 3rem;
    font-weight: 200;
    line-height: 1.4;
    text-transform: uppercase;
    margin: 1.5rem 0;
    padding-bottom: 1rem;
}


form > *{
    max-width: 50rem;
    margin: 3rem auto;
}

form input{
    padding: 1rem;
    background: transparent;
    border: 1px solid var(--form-button);
    border-radius: 5px 0 0 5px;
    color: var(--color-white);
    outline: none;
    font-size: 1.6rem;
    width: 75%;
}
form input::placeholder{
    color: var(--color-white);
    font-size: 1.5rem;

}
form button{
    padding: 1rem;
    background: var(--form-button);
    border-radius: 0 5px 5px 0;
    color: var(--color-white);
    font-size: 1.6rem;
    font-weight: 500;
    border: 1px solid var(--form-button);
    }


    .social-icons{
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .social-icon{
        margin: 2rem;
        padding: 1rem;
        width: 4rem;
        height: 4rem;
        border-radius: 50%;
        border: 1px solid var(--color-white);
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;
        font-size: 2rem;
        transition: all .3s ;
    }
    .social-icon:hover{
        background: var(--light-blue);
    }

    @media screen and (max-width:600px) {

        form input{
        width: 60%;
        }
        
    }